.main {
  transition: background-color 0.3s;
}

.main.light {
  background-color: #fff;
}

.main.dark {
  background-color: #000;
}

.subtitle {
  margin: 1rem;
  font-size: 2rem;
  font-weight: normal;
  transition: color 0.3s;
}

.title {
  margin: 1rem;
  font-size: 4rem;
  font-weight: normal;
  text-align: center;
  transition: color 0.3s;
}

.description {
  margin: 1rem;
  font-size: 2rem;
  font-weight: normal;
  text-align: center;
  transition: color 0.3s;
}

.subtitle.light,
.title.light,
.description.light {
  color: #000;
}

.subtitle.dark,
.title.dark,
.description.dark {
  color: #fff;
}

.scroll-icon {
  position: relative;
  margin: 16px;
  width: 32px;
  height: 48px;
  border: 2px solid #fff;
  border-radius: 15px;
}

.scroll-icon__wheel {
  position: absolute;
  top: 8px;
  left: 12px;
  width: 3px;
  height: 12px;
  background-color: #fff;
  animation: wheel 2s infinite;
}

@keyframes wheel {
  0% {
    top: 8px;
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  40% {
    opacity: 1;
  }
  60% {
    top: 16px;
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@media screen and (max-width: 1100px) {
  .scroll-icon {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .title {
    margin: 1rem 0;
    font-size: 50px;
  }

  .description {
    margin: 1rem 0;
    font-size: 1.75rem;
  }
}
