.projects {
  transform: translateY(101%);
  transition: transform 0.5s, background-color 0.3s;
}

.projects.active {
  transform: translateY(0);
}

.projects.light {
  background-color: #fff;
}

.projects.dark {
  background-color: #000;
}

.projects__wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.projects__link {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 60px;
  width: 520px;
  height: 360px;
  border: 2px solid #000;
  cursor: pointer;
}

.projects__link svg {
  display: block;
  width: 140px;
  height: 140px;
  stroke: #000;
  transition: stroke 0.3s;
}

.dark .projects__link {
  border-color: #fff;
}

.dark .projects__link svg {
  stroke: #fff;
}

.projects__link--youtube:hover svg {
  stroke: rgb(189, 14, 14);
}

.projects__link--github:hover svg {
  stroke: rgb(114, 114, 114);
}

/* CONTROLS */
.controls {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.controls__button {
  display: block;
  margin: 12px 0;
  width: 22px;
  height: 22px;
  border: 1px solid #000;
  /* border-radius: 50%; */
  background-color: transparent;
  transition: background-color 0.5s;
  cursor: pointer;
}

.controls__button.active {
  background-color: #000000;
}

.dark .controls__button {
  border: 1px solid #fff;
}

.dark .controls__button.active {
  background-color: #ffffff;
}

@media screen and (max-width: 1400px) {
  .projects__link {
    margin: 30px;
    width: 450px;
    height: 280px;
  }

  .projects__link svg {
    width: 120px;
    height: 120px;
  }
}

@media screen and (max-width: 600px) {
  .projects__link {
    margin: 20px 0;
    width: 100%;
    height: 250px;
  }

  .projects__link svg {
    width: 100px;
    height: 100px;
  }
}
